import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MagacinService } from 'src/app/services/magacin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-djubrivo',
  templateUrl: './djubrivo.component.html',
  styleUrls: ['./djubrivo.component.css']
})
export class DjubrivoComponent implements OnInit {
  
  form_naziv : FormControl = new FormControl("");
  msg = false;

  constructor(private mservice : MagacinService, private router: Router) { }

  ngOnInit() {
    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
  }

  addButton() {

    if(this.form_naziv.value == "") {
      this.msg = true;
      return;
    }
    this.msg =false;
    this.mservice.addDjubrivo(this.form_naziv.value).subscribe(data => {
      alert("Dodato novo djubrivo");
      this.router.navigate(['početna']);
    })
  }


}
