import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MagacinService } from 'src/app/services/magacin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-izvestajdetalj',
  templateUrl: './izvestajdetalj.component.html',
  styleUrls: ['./izvestajdetalj.component.css']
})
export class IzvestajdetaljComponent implements OnInit {

  constructor(private mservice : MagacinService, private router : Router) { }

  magacin_form : FormControl = new FormControl("");
  smer_form : FormControl = new FormControl("");
  datum: FormControl = new FormControl("");
  value;
  message = false;
  string;
  magacini;
  ngOnInit() {

    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
    
    this.mservice.getAll().subscribe(data => {
      this.magacini = data;
    })
  }

  getNaDan() {
   var obj2send = {
      "magacinId": this.magacin_form.value,
      "smer": this.smer_form.value,
      "datum": this.datum.value
    }
   this.string = this.smer_form.value == "ulaz" ? "Ukupno utovareno:" : "Ukupno istovareno:";
    console.log(obj2send);
    this.mservice.izvestajDetaljno(obj2send).subscribe(data => {
      console.log(data);
      if(data == null) {
        this.message = true;
        return;
      }
      this.message = false;
      this.value = data;
      this.value = this.value.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
    })
  }
  

}
