import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MagacinService } from 'src/app/services/magacin.service';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-pakovanje',
  templateUrl: './pakovanje.component.html',
  styleUrls: ['./pakovanje.component.css']
})
export class PakovanjeComponent implements OnInit {

  magacini;
  roba;
  showcrevo = false;
  showlot = false;
  lots;
  crevo_form : FormControl = new FormControl("");
  izvor_form : FormControl = new FormControl("");
  showdjubrivo = false;
  showPakovanje = false;
  pakovanje_form :FormControl = new FormControl("");
  magacin_form : FormControl = new FormControl("");
  djubrivo_form: FormControl = new FormControl("");
  lot_form : FormControl = new FormControl("");
  kolicina :FormControl = new FormControl("");
  datum: FormControl = new FormControl("");
  materijali;
  showKolicina = false;
  showbtn = false;
  constructor(private router: Router, private mservice: MagacinService) { }

  ngOnInit() {

    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
    
    this.mservice.getAll().subscribe(data => {
      this.magacini = data;
      this.mservice.getAllRoba().subscribe(robs => {
        this.roba = robs;
        this.mservice.getAllLots().subscribe(lott => {
          this.lots = lott;
          
        })
          
      })
    })
  console.clear();
  }

  izvorChange(event) {
    console.log(event);
    if(event != '0') {
      console.log("usa")
      this.showPakovanje = true;
      //this.showcrevo = true;
    } else {
     // this.showcrevo = false;
     this.showPakovanje = false;
     this.showbtn = false;
     this.showcrevo = false;
     this.pakovanje_form = new FormControl("");
    }
  }

  changedLot(event) {
    if(event !='0') {
      this.showKolicina = true;
    } else {
      
      this.showKolicina = false;
      this.showPakovanje = false;
      this.showcrevo = false;
      this.showbtn = false;
      this.kolicina = new FormControl("");
      this.pakovanje_form = new FormControl("");
      this.izvor_form = new FormControl("");
      
    }
  }

  zapakuj() {
    if(this.kolicina.value == "" || this.datum.value == "") {
      alert("Niste uneli kolicinu ili datum.");
      return;
    } 
    var boj2send = {
      "magacinId" : this.magacin_form.value,
      "lotId" : this.lot_form.value,
      "izvorId" : this.izvor_form.value,
      "robaId" : this.djubrivo_form.value,
      "crevoId" : this.crevo_form.value ,
      "pakovanjeId" :this.pakovanje_form.value ,
      "kolicina" : this.kolicina.value,
      "datum" : this.datum.value
    }
	if(boj2send.pakovanjeId == 1) {
		if(boj2send.robaId ==  5 || boj2send.robaId == 7 || boj2send.robaId == 8) { //1.25 SAN, KAN, UREA
		  if(boj2send.kolicina % 1.25 != 0) {
			alert("Kolicina odabrane robe mora biti deljiva sa 1.25");
			return;
		  }
		} else {
		  if(boj2send.kolicina % 1.5 != 0) {
			alert("Kolicina odabrane robe mora biti deljiva sa 1.5");
			return;
		  }
		}
	} else if(boj2send.pakovanjeId == 2) {
		var number = (boj2send.kolicina * 1000) / 50;
		var fixed = number.toFixed(6);
		var real = +fixed % 1;
		if(real != 0) {
			alert("Mora biti deljivo sa 0.5");
			return;
		}
	} else if(boj2send.pakovanjeId == 3) {
		var number = (boj2send.kolicina * 1000) / 500;
		var fixed = number.toFixed(6);
		var real = +fixed % 1;
		if(real != 0) {
			alert("Mora biti deljivo sa 0.5");
			return;
		}
	} else if(boj2send.pakovanjeId == 4) {
		var number = (boj2send.kolicina * 1000) / 600;
		var fixed = number.toFixed(6);
		var real = +fixed % 1;
		if(real != 0) {
			alert("Mora biti deljivo sa 0.6");
			return;
		}
	} else if(boj2send.pakovanjeId == 5) {
		var number = (boj2send.kolicina * 1000) / 50;
		var fixed = number.toFixed(6);
		var real = +fixed % 1;
		if(real != 0) {
			alert("Mora biti deljivo sa 0.10");
			return;
		}
	}


    console.log(boj2send);

    this.mservice.pakuj(boj2send).subscribe(dax => {
      console.log(dax);
      if(dax == null) {
        alert("Proverite da li imate dovoljno repro materijala");
        return;
      }
      
      alert("Uspesno zapakovano.")
    }, error => {
      alert(error.error.message);
    })
  }

  pakovanjeChange(event) {
    if(event != '0') {
      this.showbtn = true;
    } 
    if(event == '1') {
      console.log("1323")
      this.showcrevo = true;
    } 
    else if(event == '0') {
      this.showbtn = false;
      this.showcrevo = false;
    } else {
      this.showcrevo = false;
    }

  }

  magacinChange(event) {
    if(event != '0') {
      this.showdjubrivo = true;
    } else {
      this.showlot = false;
      this.showKolicina = false;
      this.showPakovanje = false;
      this.showcrevo = false;
      this.showbtn = false;
      this.showdjubrivo = false;
      this.djubrivo_form = new FormControl("");
      this.kolicina = new FormControl("");
      this.izvor_form = new FormControl("");
      this.pakovanje_form = new FormControl("");
      this.lot_form = new FormControl("");
    }
  }
  onChangeDjubrivo(event) {
    if(event != '0') {
      this.showlot = true;
    } else {
      this.showlot = false;
      this.showKolicina = false;
      this.showPakovanje = false;
      this.showcrevo = false;
      this.showbtn = false;
      this.kolicina = new FormControl("");
      this.pakovanje_form = new FormControl("");
      this.lot_form = new FormControl("");
      this.izvor_form = new FormControl("");
      
      
    }
  }

}
