import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { AuthLoginInfo } from 'src/app/auth/forms/login-info';
import { AuthService } from 'src/app/auth/service/auth.service';
import { TokenStorageService } from 'src/app/auth/token-storage/token-storage.service';
import { Router } from '@angular/router';
import { JWTAuth } from 'src/app/auth/response/jwt-auth';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private aservice: AuthService,private authService: AuthService,
    private tokenStorage: TokenStorageService,
    private router: Router) { }

  wrong = false;
  email: FormControl = new FormControl("");
  password: FormControl = new FormControl("");
  private jwtauth: JWTAuth;

  ngOnInit() {
  }

  loginBtn() {
    if(this.email.value == "") {
      alert("Unesite username");
      return;
    }
    if(this.password.value == "") {
      alert("Unesite password");
      return;
    }
    this.wrong = false;
    var loginForm: AuthLoginInfo = 
            new AuthLoginInfo(this.email.value, this.password.value);

    this.aservice.attemptAuth(loginForm).subscribe(data => {
      console.log(data);
      this.jwtauth = data;
      this.tokenStorage.saveToken(data.accessToken);
      this.router.navigate(['/početna']);
  },  error=>  {
    this.wrong = true;
      return;
  });

  }
}