import { BrowserModule } from '@angular/platform-browser';
import { NgModule, InjectionToken } from '@angular/core';
import { AppComponent } from './app.component';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from './modules/material.module';
import { DatePipe } from '@angular/common';
import { MatSelectModule } from '@angular/material';
import { AuthInterceptor } from './auth/auth-interceptor';
import { HomepageComponent } from './view/homepage/homepage.component';
import { LoginComponent } from './view/login/login.component';
import { ReportComponent } from './view/report/report.component';
import { NavbarComponent } from './view/navbar/navbar.component';
import { LagerlistaComponent } from './view/lagerlista/lagerlista.component';
import {DataTableModule} from "angular-6-datatable";
import { DjubrivoComponent } from './view/djubrivo/djubrivo.component';
import { ParseComponent } from './view/parse/parse.component';
import { NewreproComponent } from './view/newrepro/newrepro.component';
import { PakovanjeComponent } from './view/pakovanje/pakovanje.component';
import { LagerreproComponent } from './view/lagerrepro/lagerrepro.component';
import { ChangepasswordComponent } from './view/changepassword/changepassword.component';
import { ConstructionComponent } from './view/construction/construction.component';
import { IzvestajdetaljComponent } from './view/izvestajdetalj/izvestajdetalj.component';
import { LagertabelaComponent } from './view/lagertabela/lagertabela.component';
import { NgMultiSelectDropDownModule} from 'ng-multiselect-dropdown';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';
import { IzvestajreproComponent } from './view/izvestajrepro/izvestajrepro.component';

const appRoutes: Routes = [
  { path: 'početna', component: HomepageComponent},
  { path: 'prijava', component: LoginComponent },
  { path: 'lagerrp', component: LagerreproComponent },
  { path: 'izveštaj', component: ReportComponent},
  { path: 'izveštaj/repro', component: IzvestajreproComponent},
  { path: 'lozinka', component: ChangepasswordComponent},
  { path: 'djubrivo', component: DjubrivoComponent},
  { path: 'lager', component: LagerlistaComponent},
  { path: 'pakovanje', component: PakovanjeComponent},
  { path: 'repro', component: NewreproComponent},
  { path: 'parse', component: ParseComponent},
  { path: 'detalj', component: IzvestajdetaljComponent},
  { path: '**', component: LoginComponent},

];

@NgModule({
  declarations: [
    AppComponent,
    HomepageComponent,
    LoginComponent,
    ReportComponent,
    NavbarComponent,
    LagerlistaComponent,
    DjubrivoComponent,
    ParseComponent,
    NewreproComponent,
    PakovanjeComponent,
    LagerreproComponent,
    ChangepasswordComponent,
    ConstructionComponent,
    IzvestajdetaljComponent,
    LagertabelaComponent,
    IzvestajreproComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MaterialModule,
    DataTableModule,
    HttpClientModule,
    ReactiveFormsModule,
    AngularMultiSelectModule,
    NgMultiSelectDropDownModule.forRoot(),
    FormsModule,
    MatSelectModule,
    RouterModule.forRoot(
      appRoutes,
      { useHash: true }
)
  ],
  providers: [

    {
    provide: HTTP_INTERCEPTORS,
    useClass: AuthInterceptor,
    multi: true,
  },
  
  DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule { }