import { Component, OnInit } from '@angular/core';
import { MagacinService } from 'src/app/services/magacin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-parse',
  templateUrl: './parse.component.html',
  styleUrls: ['./parse.component.css']
})
export class ParseComponent implements OnInit {

  constructor(private mservice: MagacinService, private router : Router) { }
  err = false;
  parsed = false;
  fileToUpload: File = null;
  obj4table;
  columns:string[] = ["Magacin","Djubrivo","Pakovanje","Smer","Kolicina", "Lot", "Napomena"];
  ngOnInit() {
    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
  }

  handleFileInput(files: FileList) {
    this.fileToUpload = files.item(0);
  }

  uploadFileToActivity() {
    this.mservice.postFile(this.fileToUpload).subscribe(data => {
     
      this.obj4table = data;
      console.log(this.obj4table);
      this.parsed = true;
      if(this.obj4table.length == 0) {
        this.err = true;
      } else {
        this.err = false;
      }
      }, error => {
        console.log(error);
      });
  }


}
