import { Component, OnInit } from '@angular/core';
import * as jsPDF from 'jspdf';
import 'jspdf-autotable';
import html2canvas from 'html2canvas';
import { MagacinService } from 'src/app/services/magacin.service';
import { FormControl, Form } from '@angular/forms';
import { Router } from '@angular/router';
import * as _ from 'underscore';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {

  constructor(private mservice: MagacinService, private router: Router) { }

  magacini: Object[];
  lots: Object[];
  roba : Object[];
  napomena_form:FormControl = new FormControl("");
  magacin_form:FormControl = new FormControl("");
  lot_form:FormControl = new FormControl("");
  djubrivo_form:FormControl = new FormControl("");
  pakovanje_form: FormControl = new FormControl("");
  smer_form : FormControl = new FormControl("");
  columns:string[] = ["Datum","Pakovanje","Smer","Kolicina", "Stanje", "Lot", "Napomena"];
  obj4table;
  obj2filter;
  nema = false;
  msg = false;
  headerobj;
  datumOd:FormControl = new FormControl("");
  datumDo:FormControl = new FormControl("");
  podsmer_form:FormControl = new FormControl("");
  triggerDatum = false;
  showTable = false;
  sortex = false;
  ngOnInit() {

    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['login']);
    }
    
    this.mservice.getAll().subscribe(data => {
      this.magacini = data;
      this.mservice.getAllRoba().subscribe(robs => {
        this.roba = robs;
        this.mservice.getAllLots().subscribe(lt => {
          this.lots = lt;
        })
      })
    })
  
  }

  sortByDate() {
    console.table(this.obj4table);
    this.sortex = !this.sortex;
    if(this.sortex) {
      this.obj4table = _.sortBy(this.obj4table, 'datum');
    } else {
      this.obj4table = _.sortBy(this.obj4table, 'datum').reverse();
    }
    
  }

  filterTable() {
    console.log(this.datumOd);
    //console.log(odDatum);
    this.obj2filter = {
      magacinId : this.magacin_form.value,
      robaId    : this.djubrivo_form.value,
      lotId     : this.lot_form.value,
      pDatum    : this.datumOd.value,
      kDatum    : this.datumDo.value,
      pakovanjeId : this.pakovanje_form.value == "" ? 0 : this.pakovanje_form.value,
      smer : this.smer_form.value == "" ? "" : this.smer_form.value
    }
    this.obj4table = [];

    this.mservice.getPoDatumu(this.obj2filter).subscribe(data => {
      this.obj4table = data;
      
      var summ = 0;
     



      if(this.podsmer_form.value == "") {
        if(this.smer_form.value == "ulaz") {
          this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                
                                                                    return itm.smer == 'ulaz '; 
                                                                  })
        }
        if(this.smer_form.value == "izlaz") {
          this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                
                                                                    return itm.smer == 'izlaz '; 
                                                                  })
        }
      } else if(this.podsmer_form.value == "dobavljac") {
              if(this.smer_form.value == "ulaz") {
                this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                      
                                                                          return itm.smer == 'ulaz dobavljac'; 
                                                                        })
              }
              /*
              if(this.smer_form.value == "izlaz") {
                this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                      
                                                                          return itm.smer == 'izlaz '; 
                                                                        })
              }
              */
      } else if(this.podsmer_form.value == "interno") {
            if(this.smer_form.value == "ulaz") {
              this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                    
                                                                        return itm.smer == 'ulaz interno'; 
                                                                      })
            }
            
            if(this.smer_form.value == "izlaz") {
              this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                    
                                                                        return itm.smer == 'izlaz interno'; 
                                                                      })
            }
            
      }

      this.obj4table.forEach(element => {
        summ += element.kolicina;
      });
  
      var sumElement = {datum: "", pakovanjeNaziv: "", smer: "", kolicina: null, novoStanje: "",lotNaziv: "",napomena: ""};
      sumElement.kolicina = summ;
      sumElement.kolicina = sumElement.kolicina.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
      this.headerobj = {
        mag:this.obj4table[0].magacinNaziv,
        rob:this.obj4table[0].robaNaziv,
        lot:this.obj4table[0].lotNaziv
      }
  
      this.obj4table.push(sumElement);
      console.clear();

      
    })
   
    

  }

  addNapomena(id) {
    console.log(id);
    if(this.napomena_form.value == "") {
      alert("Unesite napomenu.");
      return;
    }
    var obj2send = {
      "napomena" : this.napomena_form.value,
      "id"       : id.id
    }
    this.mservice.addNapomena(obj2send).subscribe(data=> {
      alert("Dodata napomena");
      id.hasNapomena = true;
      id.napomena = data.napomena;

    })
    this.napomena_form = new FormControl("");
  }

  showTa() {
    if(this.magacin_form.value == "" || this.djubrivo_form.value == "" ) {
      this.msg = true;
      return;
    }
    this.showTable = true;
    
    this.obj2filter = {
      magacinId : this.magacin_form.value,
      robaId    : this.djubrivo_form.value,
      lotId     : this.lot_form.value,
      pDatum    : "",
      kDatum    : "",
      pakovanjeId : this.pakovanje_form.value == "" ? 0 : this.pakovanje_form.value,
      smer : this.smer_form.value == "" ? "" : this.smer_form.value
    }

    this.mservice.getAllPromena(this.obj2filter).subscribe(data => {
      console.log("promene");
      
      this.obj4table = data;
      this.obj4table = _.sortBy(this.obj4table, 'datum').reverse();
      if(this.podsmer_form.value == "") {
        if(this.smer_form.value == "ulaz") {
          this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                
                                                                    return itm.smer == 'ulaz '; 
                                                                  })
        }
        if(this.smer_form.value == "izlaz") {
          this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                
                                                                    return itm.smer == 'izlaz '; 
                                                                  })
        }
      } else if(this.podsmer_form.value == "dobavljac") {
              if(this.smer_form.value == "ulaz") {
                this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                      
                                                                          return itm.smer == 'ulaz dobavljac'; 
                                                                        })
              }
              /*
              if(this.smer_form.value == "izlaz") {
                this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                      
                                                                          return itm.smer == 'izlaz '; 
                                                                        })
              }
              */
      } else if(this.podsmer_form.value == "interno") {
            if(this.smer_form.value == "ulaz") {
              this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                    
                                                                        return itm.smer == 'ulaz interno'; 
                                                                      })
            }
            
            if(this.smer_form.value == "izlaz") {
              this.obj4table =  _.filter(this.obj4table, function(itm) { 
                                                                    
                                                                        return itm.smer == 'izlaz interno'; 
                                                                      })
            }
            
      }
       
      console.log(this.obj4table);

      if(this.obj4table.length == 0) {
        this.showTable = false;
        this.nema = true;
      } else {
        this.showTable = true;
        this.nema = false;
      }
      var summ = 0;
    this.obj4table.forEach(element => {
      summ += element.kolicina;
      if(element.smer == 'ulaz kupac') {
        element.smer = 'ulaz';
      }
      if(element.novoStanje <= 100) {
        element.urgent = true;
        element.needed = false;
      }
      else if(element.novoStanje <= 500 && element.novoStanje > 100) {
        element.urgent = false;
        element.needed = true;
      }
      else {
        element.urgent = false;
        element.needed = false;
      }
      if(element.napomena == "" || element.napomena == null || element.napomena == undefined) {
        element.hasNapomena = false;
      } else {
        element.hasNapomena = true;
      }
  
     
    });
    
    console.log("konacno");
    console.log(this.obj4table);
    var sumElement = {datum: "", pakovanjeNaziv: "", smer: "", kolicina: null, novoStanje: "",lotNaziv: "",napomena: ""};
    sumElement.kolicina = summ;
    sumElement.kolicina = sumElement.kolicina.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
    this.obj4table.push(sumElement);


    })


    this.headerobj = {
      mag:this.obj4table[0].magacinNaziv,
      rob:this.obj4table[0].robaNaziv,
      lot:this.obj4table[0].lotNaziv
    }
    console.clear();
  }

  
  onSort(event) {

  }
  downloadAsPdf() {
    
    let doc = new jsPDF();
    
      doc.autoTable({html: '#tableid'})
      
      doc.save(`Izveštaj-${this.obj4table[0].magacinNaziv}-${this.obj4table[0].robaNaziv}-${new Date().toLocaleDateString()}.pdf`);
      
      console.clear();

      /*
    doc.setFontSize(20);
    doc.setFont('Georgia','serif');
    var img = new Image();
    img.src = '../../assets/logo.png';
    doc.addImage(img, 'PNG', 12, 10);
    doc.text(12, 40, `${this.obj4table[0].magacinNaziv}`);
    doc.text(12, 50, `${this.obj4table[0].robaNaziv}`);
    doc.text(12, 60, `LOT${this.obj4table[0].lotNaziv}`);
    
    let element = <HTMLScriptElement>document.getElementById("tableid");

  
    html2canvas(element)
      .then((canvas: any) => {
          console.log(element);
          doc.addImage(canvas.toDataURL("image/jpeg"), "JPEG", 12,70, 
                        doc.internal.pageSize.width/1.3, element.offsetHeight/4 );
          doc.save(`Izveštaj-${Date.now()}.pdf`);
      })

*/
  }
  
}
