import { Component, OnInit } from '@angular/core';
import { MagacinService } from 'src/app/services/magacin.service';
import { Sluzba } from 'src/app/models/Sluzba';
import { FormControl } from '@angular/forms';
import { Magacin } from 'src/app/models/Magacin';
import { empty } from 'rxjs';
import { Promena } from 'src/app/models/Promena';
import { Router } from '@angular/router';


@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {

  constructor(private mservice: MagacinService, private router:Router) { }

  magacin_form : FormControl = new FormControl("");
  djubrivo_form : FormControl = new FormControl("");
  lot_form : FormControl = new FormControl("");
  datum: FormControl = new FormControl("");
  smer_form : FormControl = new FormControl("");
  form_vanstandardno: FormControl = new FormControl("");
  form_25 : FormControl = new FormControl("");
  podsmer_form : FormControl = new FormControl("");
  form_50: FormControl = new FormControl("");
  form_500 : FormControl = new FormControl("");
  form_600 : FormControl = new FormControl("");
  form_1000 : FormControl = new FormControl("");
  form_rinfuza : FormControl = new FormControl("");
  form_1000neegal : FormControl = new FormControl("");
  magacini: Object[];
  lots: Object[];
  roba : Object[];
  ngOnInit() {

    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
    
    this.mservice.getAll().subscribe(data => {
      this.magacini = data;
      this.mservice.getAllRoba().subscribe(robs => {
        this.roba = robs;
        this.mservice.getAllLots().subscribe(lt => {
          this.lots = lt;
        })
      })
    })
  console.clear();
  }

  testButton() {
    if(this.magacin_form.value == "default" || this.magacin_form.value == "") {
      alert("Morate uneti magacin");
      return;
    }
    if(this.djubrivo_form.value == "default" || this.djubrivo_form.value == "") {
      alert("Morate uneti vrstu djubriva");
      return;
    }
    if(this.lot_form.value == "default" || this.lot_form.value == "") {
      alert("Morate uneti LOT");
      return;
    }
    if(this.smer_form.value == "default" || this.smer_form.value == "") {
      alert("Morate uneti smer");
      return;
    }
    if(this.datum.value == "") {
      alert("Morate uneti datum");
      return;
    }



    var obj2send:Promena = new Promena();
    obj2send.magacinId = this.magacin_form.value;
    obj2send.robaId    = this.djubrivo_form.value;
    obj2send.lotId     = this.lot_form.value;
    obj2send.smer      = this.smer_form.value;
    let map:{ [name: number]: number }={};
    map[1]=this.form_25.value      || 0;
    map[2]=this.form_50.value      || 0;
    map[3]=this.form_500.value     || 0;
    map[4]=this.form_600.value     || 0;
    map[5]=this.form_1000.value    || 0;
    map[6]=this.form_rinfuza.value || 0;
    map[7]=this.form_vanstandardno.value || 0;  
    map[8]=this.form_1000neegal.value || 0;
    obj2send.promet = map;
    obj2send.podsmer = this.podsmer_form.value;
    obj2send.datum = this.datum.value;
    this.mservice.promena(obj2send).subscribe(data => {
      console.log(data);
      if(data != null) {
        alert("Uspesno uneseno!");
        //location.reload();
      }
      
    }, error => {
      console.log(error);
      alert(error.error.message);
    })
  }

}
