  novakolicina: FormControl = new FormControl("");
  pakovanje_form : FormControl = new FormControl("");
  magacinizmena_form : FormControl = new FormControl("");
  djubrivo_form : FormControl = new FormControl("");
  paletaform : FormControl = new FormControl("");
  izmenastreca = false;
