import { Component, OnInit } from '@angular/core';
import { FormControl, Form } from '@angular/forms';
import { Router } from '@angular/router';
import { MagacinService } from 'src/app/services/magacin.service';

@Component({
  selector: 'app-newrepro',
  templateUrl: './newrepro.component.html',
  styleUrls: ['./newrepro.component.css']
})
export class NewreproComponent implements OnInit {

  magacin_form : FormControl = new FormControl("");
  djubrivo_form : FormControl = new FormControl("");

  constructor(private router : Router, private mservice: MagacinService) { }

  magacini: Object[];
  kolicina: FormControl = new FormControl("");
  roba : Object[];
  materijal_form : FormControl = new FormControl("");
  materijali: Object[];
  odabranaRoba = false;
  paletica_form : FormControl = new FormControl("");
  magacin_prenos_form : FormControl = new FormControl("");
  showPaleta = false;
  unetMagacin = false;
  unetMagacinPrenos = false;
  showPeCrevo = false;
  showKolicinaPaleta = false;
  strec = false;
  dzak = false;
  operacija_form : FormControl = new FormControl("");
  showDjubrivo = false;
  ngOnInit() {
    this.magacin_prenos_form.setValue("0");

    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
    
    this.mservice.getAll().subscribe(data => {
      this.magacini = data;
      this.mservice.getAllRoba().subscribe(robs => {
        this.roba = robs;
          this.mservice.getAllMaterijali().subscribe(mats => {
            this.materijali = mats;
          })
      })
    })
  console.clear();
  }

  dzakChange(event) {
    if(event != "0") {
      this.showKolicinaPaleta = true;
  
    } else {
      this.showKolicinaPaleta = false;
    }
  }

  magacinChange(event) {
    if(event != "0") {
      this.unetMagacin = true;
    } else {
      this.unetMagacin = false;
    }
  }

  magacinPrenosChange(event) {
    if(event != "0") {
      this.unetMagacinPrenos = true;
      this.operacija_form.setValue("prenos")
    } else {
      this.unetMagacinPrenos = false;
      this.operacija_form.setValue("plus")
    }
  }

  
  vrstaChange(event) {
    
    if(event == "1") {
      this.paletica_form = new FormControl("");
      this.kolicina = new FormControl("");
      this.djubrivo_form.setValue("0");
      this.dzak = false;
      this.showPaleta = true;
      this.showPeCrevo = false;
      this.showKolicinaPaleta = false;
    } else if (event == "2") {
      this.paletica_form = new FormControl("");
      this.kolicina = new FormControl("");
      this.showPeCrevo = true;
      this.showPaleta = false;
      this.dzak = false;
      this.showKolicinaPaleta = false;

    } else if (event == "3") {
      this.paletica_form.setValue("3");
      this.kolicina = new FormControl("");
      this.djubrivo_form.setValue("0");
      this.strec = true;
      this.dzak = false;
      this.showPeCrevo = false;
      this.showKolicinaPaleta = true;
      this.showPaleta = false;
      this.showDjubrivo = false;

    } else if(event == "4") {
      this.paletica_form = new FormControl("");
      this.kolicina = new FormControl("");
      this.djubrivo_form.setValue("0");
      this.dzak = true;
      this.showPeCrevo = false;
      this.showKolicinaPaleta = false;
      this.showPaleta = false;
      this.strec = false;
      this.showDjubrivo = false;

    } else {
      this.showKolicinaPaleta = false;
      this.showPaleta = false;
      this.showDjubrivo = false;
      this.dzak = false;
      this.showPeCrevo = false;
      this.strec = false;
    }
  }
  crevoChange(event) {
    
    if(event != "0") {
      this.showKolicinaPaleta = true;
      this.showDjubrivo = true;
      this.dzak = false;
    } else {
      this.showKolicinaPaleta = false;
    }
  }

  paletaChange(event) {
    if(event != "0") {
      this.showKolicinaPaleta = true;
    } else {
      this.showKolicinaPaleta = false;
    }
    this.showDjubrivo = false;
  }

  onChange(event) {
    console.log(event);
    if(event != '0') {
      this.odabranaRoba = true;
    } else {
      this.odabranaRoba = false;
    }
    

  }

  unesiRepro() {
    var obj2send = {
      "magacinId"   : this.magacin_form.value,
      "magacinPrenosId" : this.magacin_prenos_form.value,
      "operacija": this.operacija_form.value,
      "robaId"      : this.djubrivo_form.value,
      "materijalId" : this.paletica_form.value,
      "kolicina"    : this.kolicina.value
    }
    console.table(obj2send);
    this.mservice.unesiRepro(obj2send).subscribe(data => {
      alert("Uspesno unet repro materijal");
    })
  }

}
