import { Component, OnInit } from '@angular/core';
import { Route, Router } from '@angular/router';
import { AuthService } from 'src/app/auth/service/auth.service';
import { User } from 'src/app/models/User';
import { TokenStorageService } from 'src/app/auth/token-storage/token-storage.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(private router:Router, private aservice: AuthService,
              private tokenS: TokenStorageService) { }
  user: User;
  ngOnInit() {

    this.aservice.getLogged().subscribe(data => {
      if(data == null) {
        this.router.navigate(['/prijava']);
        return;
      }
      this.user = data;
    })
  }

  navigateHome() {
    this.router.navigate(['početna']);
  }

  navigateDjubrivo() {
    this.router.navigate(['djubrivo']);
  }

  navigateParse() {
    this.router.navigate(['parse']);
  }

  navigateReport() {
    this.router.navigate(['izveštaj']);
  }

  navigateNewRepro() {
    this.router.navigate(['repro']);
  }

  navigateLager() {
    this.router.navigate(['lager']);
  }

  navigatePakovanje() {
    this.router.navigate(['pakovanje']);
  }

  navigateLagerR() {
    this.router.navigate(['lagerrp']);
  }

  navigateDetalj() {
    this.router.navigate(['detalj']);
  }
  navigateReportRepro() {
    this.router.navigate(['izveštaj/repro']);
  }

  navigatePassword() {
    this.router.navigate(['lozinka']);
  }

  logout() {
    this.tokenS.signOut();
    this.router.navigate(['login']);
  }

}
