import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class MagacinService {

  constructor(private http : HttpClient) { }

  getAll() : Observable<any> {
    return this.http.get(environment.apiUrl+'api/magacin/all');
  }

  getLagerSviDetaljno(magacini) : Observable<any> {
    return this.http.post(environment.apiUrl+'api/marlep/lagersvimagacini', magacini);
  }

  getLagerRepro(a) : Observable<any> {
    return this.http.get(environment.apiUrl + 'api/marlep/lagerMaterijala/' + a);
  }

  getAllRoba() : Observable<any> {
    return this.http.get(environment.apiUrl + 'api/roba/all');
  }

  getAllLots() :Observable<any> {
    return this.http.get(environment.apiUrl + 'api/lot/all');
  }

  promena(ppp) :Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/unesiPromenu', ppp);
  }

  createNew(sluzba) : Observable<any> {
    return this.http.post(environment.apiUrl+'api/magacin/new', sluzba);
  }

  getAllPromena(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/promeneMeril', obj)
  }

  getAllPromenaRepro(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/promeneRepro', obj)
  }

  getLager(id): Observable<any> {
    return this.http.get(environment.apiUrl + 'api/marlep/lager/'+id);
  }

  getPoDatumu(obj): Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/promeneMeril', obj);
  }

  addDjubrivo(naziv): Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/dodajDjubrivo', naziv);
  }

  addNapomena(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/dodajNapomenu', obj);
  }

  addNapomenaRepro(obj)  : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/dodajNapomenuRepro', obj);
  }

  getAllMaterijali() : Observable<any> {
    return this.http.get(environment.apiUrl + 'api/marlep/allMaterijali');  
  }

  postFile(fileToUpload: File): Observable<any> {
    
    const formData: FormData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    return this.http
      .post(environment.apiUrl + 'api/marlep/parseExcel', formData);
  }

  izvestajDetaljno(obj) : Observable<any> {
    return this.http.post(environment.apiUrl +  'api/marlep/izvestajDetaljno', obj);
  }

  unesiRepro(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/ulazRepromaterijala', obj);
  }

  getAllLagers() : Observable<any> {
    return this.http.get(environment.apiUrl + 'api/marlep/lager');
  }

  pakuj(obj):Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/pakovanje' , obj);
  }

  getstanje(obj):Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/dobavistanje', obj)
  }

  azurirajstanje(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/azurirajstanje', obj);
  }

  azuriranjecrevo(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/azuriranjecreva', obj);
  }

  azuriranjepalete(obj) : Observable<any> {
    return this.http.post(environment.apiUrl + 'api/marlep/azuriranjepalete', obj);
  }
}
