import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { AuthService } from 'src/app/auth/service/auth.service';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {

  prvi_pass : FormControl = new FormControl("");
  drugi_pass: FormControl = new FormControl("");
  msg = false;

  constructor(private router: Router, private auths : AuthService) { }

  ngOnInit() {
    if(sessionStorage.getItem("AuthToken") == null) {
      this.router.navigate(['prijava']);
    }
    
  }
  addButton() {
    this.msg = false;
    if(this.prvi_pass.value != this.drugi_pass.value) {
      this.msg = true;
      this.prvi_pass = new FormControl("");
      this.drugi_pass = new FormControl("");
      return;
    }

    this.auths.promeniLozinku({
      oldPassword: "testtest123",
      newPassword: this.prvi_pass.value,
      newPasswordAgain: this.drugi_pass.value
    }).subscribe(data => {
      alert("Uspešno promenuta lozinka");
      this.prvi_pass = new FormControl("");
      this.drugi_pass = new FormControl("");
    })

    
  }




}
